import React, { useState } from 'react'; 
 import RankSearch from '../components/RankSearch'; 
 import SpotifyPlayerComponent from '../components/SpotifyPlayer'; 
 import SpotifyPlayBack from '../components/SpotifyPlayBack';
 import UserLogin from '../components/UserLogin';
 import { ConfigProvider, Space } from 'antd'; 
 import { 
   MenuFoldOutlined, 
   MenuUnfoldOutlined, 
   UploadOutlined, 
   UserOutlined, 
   VideoCameraOutlined, 
 } from '@ant-design/icons'; 
 import { Layout, Menu, Button, theme } from 'antd'; 
 import GuessPage from "./GuessPage"; 
 import MainPage from "./MainPage"; 
 import {BrowserRouter as Router, Routes, Route, useNavigate} from "react-router-dom"; 
 import { UpdateToken } from '../components/UserData';
  
  
  function RankPage() { 
   const { Header, Sider, Content } = Layout; 
   const { 
     token: { colorBgContainer, borderRadiusLG }, 
   } = theme.useToken(); 
   const [collapsed, setCollapsed] = useState(false); 
  
   let navigate = useNavigate(); 
  
   const SwitchPage = (item) => {
    UpdateToken();
    switch (item.key) {
      case '1':
        window.scrollTo(0, 0);
        navigate("/DB-Final/");
        // 
        break;
      case '2':
        // 
        window.scrollTo(0, 0);
        navigate("/DB-Final/Ranking");
        break;
      case '3':
        // 
        window.scrollTo(0, 0);
        navigate("/DB-Final/Search");
        break;
      case '4':
        // 
        window.scrollTo(0, 0);
        navigate("/DB-Final/PlayListPage");
        break;
      case '5':
        // 
        window.scrollTo(0, 0);
        navigate("/DB-Final/Guess");
        break;
      default:
        break;
    }
  };
  
   return ( 
     <div className="App"> 
       <Layout> 
         <Sider trigger={null} collapsible collapsed={collapsed}> 
            <UserLogin  isFix={false}/>
           <div className="demo-logo-vertical" /> 
           <Menu 
             theme="dark" 
             mode="inline" 
             defaultSelectedKeys={['2']} 
             items={[ 
               { 
                 key: '1', 
                 icon: <UserOutlined />, 
                 label: '', 
               }, 
               { 
                 key: '2', 
                 icon: <VideoCameraOutlined />, 
                 label: '', 
               }, 
               { 
                 key: '3', 
                 icon: <UploadOutlined />, 
                 label: '', 
               }, 
               { 
                 key: '4', 
                 icon: <UploadOutlined />, 
                 label: '', 
               }, 
               { 
                 key: '5', 
                 icon: <UploadOutlined />, 
                 label: '', 
               } 
             ]} 
             onClick={(item) => SwitchPage(item)} 
           /> 
           
          
         </Sider> 
         <Layout  style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/Image/board.gif)`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', minHeight: '100vh' }}> 
          <Header style={{ padding: 0, background: 'linear-gradient(to right, #e6ffc2, #80d4ff)' , height: '82px'}}>
          <div style={{ display: 'flex', alignItems: 'center', marginTop: '-10px'}}>
             <Button 
               type="text" 
               icon={collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />} 
               onClick={() => setCollapsed(!collapsed)} 
               style={{ 
                 fontSize: '16px', 
                 width: 64, 
                 height: 64, 
               }} 
             /> 
             <h1 style={{ marginLeft: '40%' }}>MyMusic</h1> 
           </div> 
           </Header> 
  
           <div> <br/><br/> </div> 
           <div> 
           {/* <SpotifySearch /> */} 
           <RankSearch/> 
  
           {/* <p style={{position: 'absolute', top: '10px', left: '700px'}}>Update at 20240102</p> */} 
           </div> 
         </Layout> 
       </Layout> 
  
  
     </div>    
   ); 
  
 } 
  
  
 export default RankPage;