// src/components/SongInfo.js
import React, { useState } from 'react';
import { Button, Card , message} from 'antd';
import axios from 'axios';
import { username, nickname } from './UserData';

const SongInfoComponent = ({ song_name, artist_name, album_name, track_num, popularity, time, year , song_id, artist_id, album_id}) => {
  // Convert seconds to minutes and seconds
  const minutes = Math.floor(time / 60);
  const seconds = time % 60;

  const UpdateList = async () => {
      console.log(username);
      console.log(song_id);
    try {
      const response = await axios.post('https://7q96yl67a3.execute-api.us-east-1.amazonaws.com/cors/UpdateDB',
      {
          "operation": "UpdateSongList",
          "user_name" : username, 
          "track_id" : song_id,
      });
  
      console.log(response.data[0]);
      message.success('Successful add to your songlist!', 2);
    } catch (error) {
      console.error('Error update data', error);
    }
  };

  const UpdateFunc = async () => {
    console.log(song_id);
    console.log(album_id);
    console.log(artist_id);
    if(song_id!='') UpdateSongs();
    if(album_id!='') UpdateAlbum();
    if(artist_id!='') UpdateArtist();
  };
  
  const UpdateSongs = async () => {
    try {
      const response = await axios.post('https://7q96yl67a3.execute-api.us-east-1.amazonaws.com/cors/UpdateDB',
      {
          "operation": "UpdateByTrack",
          "id" : song_id
      });
  
      console.log(response.data[0]);
      message.success('Successful add to database. Thanks!', 2);
    } catch (error) {
      console.error('Error update data', error);
    }
  };

  const UpdateAlbum = async () => {
    try {
      const response = await axios.post('https://7q96yl67a3.execute-api.us-east-1.amazonaws.com/cors/UpdateDB',
      {
          "operation": "UpdateByAlbum",
          "id" : album_id
      });
  
      console.log(response.data[0]);
    } catch (error) {
      console.error('Error update data', error);
    }
  };

  const UpdateArtist = async () => {
    try {
      const response = await axios.post('https://7q96yl67a3.execute-api.us-east-1.amazonaws.com/cors/UpdateDB',
      {
          "operation": "UpdateByArtist",
          "id" : artist_id
      });
  
      console.log(response.data[0]);
    } catch (error) {
      console.error('Error update data', error);
    }
  };

  return (
    <div className="SongInfoComponent" style={{ marginRight: '-50px', marginTop: '-10px'}}>
      <Card title="" bordered={true} headStyle={{ backgroundColor: 'black', color: 'white' }} style={{ width: 300, backgroundColor: 'black', color: 'white' }}>
        <p style={{ fontSize: '18px', fontWeight: 'bold' }}>{song_name}</p>
        <p>: {artist_name}</p>
        <p>: {album_name}</p>
        <p>: {track_num}</p>
        <p>: {popularity}</p>
        <p>: {`${minutes}:${seconds < 10 ? '0' : ''}${seconds}`}</p>
        <p>: {year}</p>
        <Button type="primary" onClick={UpdateFunc}>
            
        </Button>
        <Button type="primary" style={{ marginLeft: '20px'}} onClick={UpdateList}>
            
        </Button>
      </Card>
    </div>
  );
};

export default SongInfoComponent;
