import React, { useState  , useEffect } from 'react';
import MusicSearch from '../components/MusicSearch';
import SpotifyPlayerComponent from '../components/SpotifyPlayer';
import SpotifyPlayBack from '../components/SpotifyPlayBack';
import UserLogin from '../components/UserLogin';
import SearchPage from "./SearchPage";
import GuessPage from "./GuessPage";
import RankPage from "./RankPage";
import PlayListPage from "./PlayListPage";
import { ConfigProvider, Space } from 'antd';
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  UploadOutlined,
  UserOutlined,
  VideoCameraOutlined,
} from '@ant-design/icons';
import { Layout, Menu, Button, theme, Card, Input} from 'antd';
import {BrowserRouter as Router, Routes, Route, useNavigate} from "react-router-dom";
import ReactDOM from 'react-dom/client';
import { UpdateToken , spotifyPlayToken, UpdateSpotifyPlay} from '../components/UserData';
import '../components/textFont.css';

function MainPage() {

  const { Header, Sider, Content } = Layout;
  const {
    token: { colorBgContainer, borderRadiusLG },
  } = theme.useToken();
  const [collapsed, setCollapsed] = useState(false);
  const [PlayKey, setPlayKey] = useState('');
  
  let navigate = useNavigate();

  useEffect(() => {
    UpdateToken();
  }, []);

  const SwitchPage = (item) => {
    UpdateToken();
    switch (item.key) {
      case '1':
        window.location.reload();
        window.scrollTo(0, 0);
        navigate("/DB-Final/");
        // 
        break;
      case '2':
        // 
        window.scrollTo(0, 0);
        navigate("/DB-Final/Ranking");
        break;
      case '3':
        // 
        window.scrollTo(0, 0);
        navigate("/DB-Final/Search");
        break;
      case '4':
        // 
        window.scrollTo(0, 0);
        navigate("/DB-Final/PlayListPage");
        break;
      case '5':
        // 
        window.scrollTo(0, 0);
        navigate("/DB-Final/Guess");
        break;
      default:
        break;
    }
  };

  const handleEnterPress = (e) => {
    if (e.key === 'Enter' && PlayKey != '') {
      console.log(PlayKey);
      UpdateSpotifyPlay(PlayKey);
    }
  };

  return (
    <div className="MainPage">
      <Layout>
        <Sider trigger={null} collapsible collapsed={collapsed}>
            <UserLogin isFix={true}/>
            
          <div className="demo-logo-vertical" />
          <Menu
            theme="dark"
            mode="inline"
            defaultSelectedKeys={['1']}
            items={[
              {
                key: '1',
                icon: <UserOutlined />,
                label: <span style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}></span>,
              },
              {
                key: '2',
                icon: <VideoCameraOutlined />,
                label: <span style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}></span>,
              },
              {
                key: '3',
                icon: <UploadOutlined />,
                label: <span style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}></span>,
              },
              {
                key: '4',
                icon: <UploadOutlined />,
                label: <span style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}></span>,
              },
              {
                key: '5',
                icon: <UploadOutlined />,
                label: <span style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}></span>,
              }
            ]}
            onClick={(item) => SwitchPage(item)}
          />
          
        </Sider>
        <Layout  style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/Image/menu.gif)`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', minHeight: '100vh' }}>
          <Header style={{ padding: 0, background: 'linear-gradient(to right, #e6ffc2, #80d4ff)' , height: '82px'}}>
          <div style={{ display: 'flex', alignItems: 'center', marginTop: '-10px'}}>
            <Button
              type="text"
              icon={collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
              onClick={() => setCollapsed(!collapsed)}
              style={{
                fontSize: '16px',
                width: 64,
                height: 64,
              }}
            />
            <h1 style={{ marginLeft: '40%' }}>MyMusic</h1>
          </div>
          </Header>
            <Card title="" bordered={true} headStyle={{ background: 'linear-gradient(to right, #FFD700, #FF6347)', color: 'black' }} 
            style={{ width: 300, backgroundColor: 'white', color: 'black' , marginTop: '20px', marginLeft: '38%', textAlign: 'center'}}>
              <p className="custom-text">2024.1.11 version</p>
              <hr style={{ width: '100%', margin: '20px 0' }} />
              <p className="custom-text">{'<>'}</p>
              <p className="custom-text">1. </p>
              <hr style={{ width: '100%', margin: '20px 0' }} />
              <a href={'https://developer.spotify.com/documentation/web-playback-sdk/tutorials/getting-started'} target="_blank" rel="noopener noreferrer">
              Help Get Key
              </a>
              <Input
                type="text"
                placeholder={spotifyPlayToken}
                value={PlayKey}
                onChange={(e) => setPlayKey(e.target.value)}
                onKeyDown={handleEnterPress}
                style={{ width: '150px' , marginLeft: '10px'}}
              />
            </Card>
            <p></p>
            <Card title="" bordered={true} headStyle={{ background: 'linear-gradient(to right, #FFD700, #FF6347)', color: 'black' }} 
            style={{ width: 300, backgroundColor: 'white', color: 'black' , marginTop: '20px', marginLeft: '38%', textAlign: 'center'}}>
              
              <p className="custom-text"></p>
              <p></p>
              <hr style={{ width: '100%', margin: '20px 0' }} />
              <p className="custom-text"></p>
            </Card>
            <p></p>
            <Card title="STAFF" bordered={true} headStyle={{ background: 'linear-gradient(to right, #FFD700, #FF6347)', color: 'black' }} 
            style={{ width: 300, backgroundColor: 'white', color: 'black' , marginTop: '20px', marginLeft: '38%', textAlign: 'center'}}>
              <p className="custom-text">BrianGodd </p>
              <p className="custom-text">Johnson7414 </p>
              <p className="custom-text">Mr. </p>
              <p className="custom-text">Mr. </p>
              <p className="custom-text">Mr. </p>
            </Card>
            <p></p>
        </Layout>
      </Layout>
      
      
    </div>   
  );
}

export default MainPage;
